.navbar{
    height: 50px;
    border-bottom: 0.5px solid white;
    display: flex;
    align-items: center;
    font-size: 14px;
    color: #555;
    .wrapper {
        width: 100%;
        padding: 9px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-bottom:  0.7px solid lightgray;
        .search {
            display: flex;
            align-items: center;
            border: 0.5px solid lightgrey;
      
            input{
                border: none;
                outline: none;
                background: transparent;

                &::placeholder{
                    font-size: 12px;
                }
            }
        }

        .items{
            display: flex;
            align-items: center;
            .item{
                display: flex;
                align-items: center;
                margin-right: 10px;

                .icon{
                    font-size: 20px;
                }

                .avatar{
                    width: 33px;
                    height: 33px;
                    border-radius: 50%;
                }
            }

        }
        
    }
}